@import "core/settings";

.body {
  padding: 0;
  margin: 0;
  background-color: white;
  font-family: Arial, Helvetica, sans-serif;
}

.header {
  background-color: white;
  width: 100%;
}

.show-controls {
  margin: 0 auto;
  text-align: center;
}

.title-searcher {
  padding: 10px;
}

.btn-styles {
  background-color: darkmagenta;
  border-radius: 6px;
  outline: none; // para quitar el reborde azul que viene por defecto.
  color: white;
  font-size: 14px;
  margin: 10px 0;
  height: 35px;
  padding: 0 14px;
}

.btn-styles:hover {
  background-color: salmon;
}

.btn-styles:active {
  font-size: 16px;
  background-color: salmon;
  box-shadow: 0 3px #666;
  transform: translateY(4px);
}

// input
.show-input {
  margin: 10px 0;
  padding: 4px;
  height: 22px;
  width: 300px;
  border: 2px solid gray;
  font-size: 14px;
  outline-color: crimson;
}

// <li>
.show-result-item {
  display: inline-block;
  margin: 10px;
  padding: 20px;
  text-align: center;
  border: 3px solid palevioletred;
  background-color: lightpink;
  width: 200px;
  height: 300px;
  color: darkmagenta;
  font-size: 14px;
}

.show-result-item-active {
  border: 3px solid purple;
  background-color: violet;
}

.show-result-item img {
  width: 80%;
  height: 80%;
}
